<template>
    <div>
        <div id="wrap">

            <!-- 해더+네비 -->
            <AppHeader />

            <div id="container" class="clearfix">
                <div id="aside">
                    <h2>갤러리</h2>
                    <ul>
                        <li><a href="">일반갤러리</a></li>
                        <li><a href="">파일첨부연습</a></li>
                    </ul>
                </div>
                <!-- //aside -->

                <div id="content">

                    <div id="content-head">
                        <h3>첨부파일연습</h3>
                        <div id="location">
                            <ul>
                                <li>홈</li>
                                <li>갤러리</li>
                                <li class="last">첨부파일연습</li>
                            </ul>
                        </div>
                        <div class="clear"></div>
                    </div>
                    <!-- //content-head -->

                    <div id="file">
                        <form v-on:submit.prevent="uploadFile" action="" method="post" enctype="multipart/form-data">
                            <table>
                                <colgroup>
                                    <col style="width: 600px;">
                                    <col style="width: 220px;">
                                </colgroup>
                                <tr>
                                    <td class="text-left"><input type="file" name="file" v-on:change="selectFile"></td>
                                    <td class="text-right"><button type="submit">파일업로드</button></td>
                                </tr>
                            </table>
                        </form>
                    </div>
                    <!-- //file -->


                </div>
                <!-- //content  -->
            </div>
            <!-- //container  -->


            <AppFooter />
            <!-- 푸터 -->

        </div>
        <!-- //wrap -->
    </div>
</template>


<script>
//외부 모듈
import "@/assets/css/gallery.css";
import axios from 'axios'

//내부 모듈
import AppHeader from "@/components/AppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";

//상수 모듈

export default {
    name: "AttachFormView",
    components: {
        // 불러온 컴포넌트를 등록합니다.
        AppHeader,
        AppFooter,
    },
    data() {
        return {
            file: "",
            saveName: ""
        };
    },
    methods: {
        selectFile(event) {
            this.file = event.target.files[0];
            console.log(this.file);
        },
        uploadFile() {
            console.log("클릭");
            const formData = new FormData();
            formData.append('file', this.file);
            formData.append('name', '정우성');
            formData.append('gender', 'female');
            

            axios({
                method: 'post', //put,post,delete
                url: 'http://localhost:9000/api/attach',
                headers: {
                    'Content-Type': 'multipart/form-data',
                    "Authorization": "Bearer " + this.$store.state.token
                },
                //params: { crtPage: this.crtPage, keyword: this.keyword }, //get방식 파라미터로 값이 전달
                data: formData, //이경우는 json이 아님

                responseType: 'json' //수신타입
            }).then(response => {
                console.log(response.data.result); //수신데이타
                console.log(response.data.apiData); //수신데이타

                if (response.data.result == "success") {
                    this.saveName = response.data.apiData;
                    this.$router.push({ path: '/attach/result', query: { saveName: response.data.apiData}})

                } else {
                    alert("알수없는 오류");
                }


            }).catch(error => {
                console.log(error);
            });

        }
    },
    created() {
    }
};
</script>
<style></style>