<template>
    <div>
        <div id="wrap">

            <!-- 해더+네비 -->
            <AppHeader />

            <div id="container" class="clearfix">
                <div id="aside">
                    <h2>게시판</h2>
                    <ul>
                        <li><a href="">일반게시판</a></li>
                        <li><a href="">댓글게시판</a></li>
                    </ul>
                </div>
                <!-- //aside -->

                <div id="content">

                    <div id="content-head">
                        <h3>일반게시판</h3>
                        <div id="location">
                            <ul>
                                <li>홈</li>
                                <li>게시판</li>
                                <li class="last">일반게시판</li>
                            </ul>
                        </div>
                        <div class="clear"></div>
                    </div>
                    <!-- //content-head -->

                    <div id="board">
                        <div id="list">
                            <form action="" method="">
                                <div class="form-group text-right">
                                    <input type="text">
                                    <button type="submit" id=btn_search>검색</button>
                                </div>
                            </form>
                            <table>
                                <thead>
                                    <tr>
                                        <th>번호</th>
                                        <th>제목</th>
                                        <th>글쓴이</th>
                                        <th>조회수</th>
                                        <th>작성일</th>
                                        <th>관리</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-bind:key="i" v-for="(boardVo, i) in boardList">
                                        <td>{{boardVo.no}}</td>
                                        <td class="text-left"><a href="#">{{boardVo.title}}</a></td>
                                        <td>{{boardVo.name}}</td>
                                        <td>{{boardVo.hit}}</td>
                                        <td>{{boardVo.regDate}}</td>
                                        <td><a href="">[삭제]</a></td>
                                    </tr>
                                </tbody>
                            </table>

                            <button v-on:click="getBoardList" type="button">10개가져오기</button>
                            
                            <!--
                            <div id="paging">
                                <ul>
                                    <li v-if="pMap.prev"><a href="">◀</a></li>
                                    <li v-bind:key="i" v-for="(page, i) in pMap.pageBtnCount">
                                        <span class="active" v-if="page==this.crtPage">
                                            <router-link v-bind:to="`/board/list?crtPage=${page+pMap.startPageBtnNo-1}&keyword=${this.keyword}`">{{page+pMap.startPageBtnNo-1}}</router-link>
                                        </span>
                                        <span v-else>
                                            <router-link v-bind:to="`/board/list?crtPage=${page+pMap.startPageBtnNo-1}&keyword=${this.keyword}`">{{page+pMap.startPageBtnNo-1}}</router-link>
                                        </span>
                                    </li>
                                    <li v-if="pMap.next"><a href="">▶</a></li>
                                </ul>


                                <div class="clear"></div>
                            </div>
                            -->


                            <a id="btn_write" href="">글쓰기</a>

                        </div>
                        <!-- //list -->
                    </div>
                    <!-- //board -->
                </div>
                <!-- //content  -->

            </div>
            <!-- //container  -->


            <AppFooter />
            <!-- 푸터 -->

        </div>
        <!-- //wrap -->

    </div>
</template>


<script>
//외부 모듈
import "@/assets/css/board.css";
import axios from 'axios'

//내부 모듈
import AppHeader from "@/components/AppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";

//상수 모듈

export default {
    name: "ListView",
    components: {
        // 불러온 컴포넌트를 등록합니다.
        AppHeader,
        AppFooter,
    },
    data() {
        return {
            boardList: [],
            crtPage: 0
        };
    },
    methods: {

        getBoardList() {
            axios({
                method: 'get', //put,post,delete
                url: 'http://localhost:9000/api/boards',
                headers: {
                    "Content-Type": "application/json; charset=utf-8",
                    "Authorization": "Bearer " + this.$store.state.token
                },
                params: {crtPage: this.crtPage++}, //get방식 파라미터로 값이 전달
                //data: this.userVo, //put, post, delete 방식 자동으로 JSON으로 변환 전달

                responseType: 'json' //수신타입
            }).then(response => {
                console.log(response.data.result); //수신데이타
                console.log(response.data.apiData); //수신데이타
                
                if (response.data.result == "success") {
                    //this.boardList = response.data.apiData.;
                    this.boardList.push(...response.data.apiData);
                    console.log(this.boardList); //수신데이타
                
                } else {
                    alert("알수없는 오류");
                }
                
                
            }).catch(error => {
                console.log(error);
            });
        }



    },
    created() { 
        this.getBoardList();
    }
};
</script>