                <li><a href="">갤러리</a></li>
                <li><a href="">방명록</a></li>
            </ul>
        </div>
        <!-- //nav -->
    </div>
</template>


<script>
//외부 모듈
//내부 모듈
//상수 모듈

export default {
    name: "AppHeader",
    components: {},
