<template>
    <div>
        <div id="wrap">

            <!-- 해더+네비 -->
            <AppHeader />

            <div id="container" class="clearfix">
                <div id="aside">
                    <h2>회원</h2>
                    <ul>
                        <li>회원정보</li>
                        <li>로그인</li>
                        <li>회원가입</li>
                    </ul>
                </div>
                <!-- //aside -->

                <div id="content">

                    <div id="content-head">
                        <h3>회원정보</h3>
                        <div id="location">
                            <ul>
                                <li>홈</li>
                                <li>회원</li>
                                <li class="last">회원정보</li>
                            </ul>
                        </div>
                        <div class="clear"></div>
                    </div>
                    <!-- //content-head -->

                    <div id="user">
                        <div id="modifyForm">
                            <form v-on:submit.prevent="modifyUser" action="" method="">

                                <!-- 아이디 -->
                                <div class="form-group">
                                    <label class="form-text" for="input-uid">아이디</label>
                                    <span class="text-large bold">{{ userVo.id }}</span>
                                </div>

                                <!-- 비밀번호 -->
                                <div class="form-group">
                                    <label class="form-text" for="input-pass">패스워드</label>
                                    <input type="text" id="input-pass" name="password" v-model="userVo.password"
                                        placeholder="비밀번호를 입력하세요">
                                </div>

                                <!-- 이메일 -->
                                <div class="form-group">
                                    <label class="form-text" for="input-name">이름</label>
                                    <input type="text" id="input-name" name="name" v-model="userVo.name"
                                        placeholder="이름을 입력하세요">
                                </div>

                                <!-- //나이 -->
                                <div class="form-group">
                                    <span class="form-text">성별</span>

                                    <label for="rdo-male">남</label>
                                    <input type="radio" id="rdo-male" name="gender" value="male"
                                        v-model="userVo.gender">

                                    <label for="rdo-female">여</label>
                                    <input type="radio" id="rdo-female" name="gender" value="female"
                                        v-model="userVo.gender">

                                </div>

                                <!-- 버튼영역 -->
                                <div class="button-area">
                                    <button type="submit" id="btn-submit">회원정보수정</button>
                                </div>

                            </form>


                        </div>
                        <!-- //modifyForm -->
                    </div>
                    <!-- //user -->
                </div>
                <!-- //content  -->

            </div>
            <!-- //container  -->

            <AppFooter />
            <!-- 푸터 -->

        </div>
        <!-- //wrap -->
    </div>
</template>


<script>
//외부 모듈
import "@/assets/css/user.css";
import axios from 'axios'

//내부 모듈
import AppHeader from "@/components/AppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";

//상수 모듈

export default {
    name: "ModifyFormView",
    components: {
        // 불러온 컴포넌트를 등록합니다.
        AppHeader,
        AppFooter,
    },
    data() {
        return {
            userVo: {
                no: "",
                id: "",
                name: "",
                password: "",
                gender: ""
            }
        };
    },
    methods: {
        getAuthUser() {
            axios({
                method: 'get', //put,post,delete
                url: 'http://localhost:9000/api/users/modify',
                headers: {
                    "Content-Type": "application/json; charset=utf-8",
                    "Authorization": "Bearer " + this.$store.state.token
                }, //전송타입 + 토큰
                //params: guestbookVo, //get방식 파라미터로 값이 전달
                //data: this.personVo, //put, post, delete 방식 자동으로 JSON으로 변환 전달

                responseType: 'json' //수신타입
            }).then(response => {
                console.log(response.data.result); //수신데이타
                console.log(response.data.apiData); //수신데이타
                if (response.data.result == "success") {
                    //성공했을때
                    this.userVo = response.data.apiData;

                } else {
                    //실패했을때- 로그인 안되었을때(토큰없을때)
                    //alert("로그인해주세요");

                    this.$store.commit("setAuthUser", null);
                    this.$store.commit("setToken", null);
                    this.$router.push("/user/loginform");
                }
            }).catch(error => {
                console.log(error);
            });

        },
        modifyUser() {
            axios({
                method: 'put', //put,post,delete
                url: 'http://localhost:9000/api/users/modify',
                headers: {
                    "Content-Type": "application/json; charset=utf-8",
                    "Authorization": "Bearer " + this.$store.state.token
                }, //전송타입 + 토큰
                //params: guestbookVo, //get방식 파라미터로 값이 전달
                data: this.userVo, //put, post, delete 방식 자동으로 JSON으로 변환 전달

                responseType: 'json' //수신타입
            }).then(response => {
                console.log(response.data.result); //수신데이타
                console.log(response.data.apiData); //수신데이타
                if (response.data.result == "success") {
                    //성공했을때
                    this.$store.commit("setAuthName", response.data.apiData);
                    this.$router.push("/");

                } else {
                    //실패했을때- 로그인 안되었을때(토큰없을때)
                    //alert("로그인해주세요");

                    this.$store.commit("setAuthUser", null);
                    this.$store.commit("setToken", null);
                    this.$router.push("/user/loginform");
                }
            }).catch(error => {
                console.log(error);
            });
        }
    },
    created() {
        this.getAuthUser();
    }
};
</script>


<style></style>
