<template>
    <div>
        <div id="wrap">

            <!-- 해더+네비 -->
            <AppHeader />

            <div id="container" class="clearfix">
                <div id="aside">
                    <h2>회원</h2>
                    <ul>
                        <li>회원정보</li>
                        <li>로그인</li>
                        <li>회원가입</li>
                    </ul>
                </div>
                <!-- //aside -->

                <div id="content">

                    <div id="content-head">
                        <h3>로그인</h3>
                        <div id="location">
                            <ul>
                                <li>홈</li>
                                <li>회원</li>
                                <li class="last">로그인</li>
                            </ul>
                        </div>
                        <div class="clear"></div>
                    </div>
                    <!-- //content-head -->

                    <div id="user">
                        <div id="loginForm">
                            <form v-on:submit.prevent="login" action="" method="">

                                <!-- 아이디 -->
                                <div class="form-group">
                                    <label class="form-text" for="input-uid">아이디</label>
                                    <input type="text" id="input-uid" name="" v-model="userVo.id"
                                        placeholder="아이디를 입력하세요">
                                </div>

                                <!-- 비밀번호 -->
                                <div class="form-group">
                                    <label class="form-text" for="input-pass">비밀번호</label>
                                    <input type="text" id="input-pass" name="" v-model="userVo.password"
                                        placeholder="비밀번호를 입력하세요">
                                </div>


                                <!-- 버튼영역 -->
                                <div class="button-area">
                                    <button type="submit" id="btn-submit">로그인</button>
                                </div>

                            </form>
                        </div>
                        <!-- //loginForm -->
                    </div>
                    <!-- //user -->
                </div>
                <!-- //content  -->

            </div>
            <!-- //container  -->

            <AppFooter />
            <!-- 푸터 -->

        </div>
        <!-- //wrap -->
    </div>
</template>


<script>
//외부 모듈
import "@/assets/css/user.css";
import axios from 'axios'

//내부 모듈
import AppHeader from "@/components/AppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";

//상수 모듈

export default {
    name: "LoginFormView",
    components: {
        // 불러온 컴포넌트를 등록합니다.
        AppHeader,
        AppFooter,
    },
    data() {
        return {
            userVo: {
                id: "",
                password: ""
            }
        };
    },
    methods: {
        login() {
            console.log(this.userVo);
            axios({
                method: 'post', //put,post,delete
                url: 'http://localhost:9000/api/users/login',
                headers: {
                    "Content-Type": "application/json; charset=utf-8",
                    "Authorization": "Bearer " + this.$store.state.token
                },
                //params: guestbookVo, //get방식 파라미터로 값이 전달
                data: this.userVo, //put, post, delete 방식 자동으로 JSON으로 변환 전달

                responseType: 'json' //수신타입
            }).then(response => {
                console.log(response.data.result); //수신데이타
                if (response.data.result == "success") {
                    //성공했을때
                    const token = response.headers.authorization.split(" ")[1];
                    let authUser = response.data.apiData;

                    console.log(authUser); //세부정보
                    console.log(token); //토큰

                    this.$store.commit("setAuthUser", authUser);
                    this.$store.commit("setToken", token);
                    this.$router.push("/");
                
                } else {
                    console.log("로그인 실패");
                    alert("아이디 또는 패스워드를 확인해 주세요");
                }
            }).catch(error => {
                console.log(error);
            });


        }
    },
    created() { }
};
</script>


<style></style>
